import Vue from 'vue';
import moment from 'moment';

import Storage from './utils/Storage';

export default new Vue({
  data() {
    return {
      entries: Storage.get(
        "debits",
        {
          parser: "JSON",
          default: [],
        }
      ),
    };
  },

  computed: {
    nextKey () {
      var ids = this.entries.map(d => { return d.id });
      return (parseInt(Math.max(...ids)) || 0) + 1;
    },
  },

  watch: {
    entries: {
      deep: true,
      handler (current) {
        Storage.set("debits", current, { formatter: "JSON" });
        this.$emit("updatedEntries", current);
      },
    },
  },

  methods: {
    add (debit) {
      debit.isEditing = false;
      debit.id = this.nextKey;
      debit.madeOn = moment();
      this.entries.unshift(debit);
    },

    remove (id) {
      var idx = this.entries.findIndex((debit) => {
        return debit.id == id;
      });
      this.entries.splice(idx, 1);
    },
  },
});



