import Vue from 'vue';
import moment from 'moment';

import Storage from './Storage';

var firstDay = moment().subtract(1, "months").endOf("month"),
    secondDay = moment().date(15),
    payDayStoragePrefix = moment().get('year') + "." +
                            moment().format("MM");

if (firstDay.day() == 0) {
  firstDay.subtract(2, "days");
} else if (firstDay.day() == 6) {
  firstDay.subtract(1, "days");
}

if (secondDay.day() == 0) {
  secondDay.subtract(2, "days");
} else if (secondDay.day() == 6) {
  secondDay.subtract(1, "days");
}

export default new Vue({
  data: {
    payDay1: Storage.get(
      payDayStoragePrefix + ".payDay1",
      {
        parser: "DATE",
        default: firstDay,
      }
    ),

    payDay2: Storage.get(
      payDayStoragePrefix + ".payDay2",
      {
        parser: "DATE",
        default: secondDay,
      }
    ),

    income: Storage.get("income", { parser: "FLOAT", default: 0 }),

    isInitiated: Storage.get("initiated", { default: false }),
  },

  watch: {
    payDay1 (v) {
      Storage.set(
        payDayStoragePrefix + ".payDay1",
        v,
        { formatter: "DATE" }
      );
    },

    payDay2 (v) {
      Storage.set(
        payDayStoragePrefix + ".payDay2",
        v,
        { formatter: "DATE" }
      );
    },

    income (v) {
      Storage.set("income", v);
    },

    isInitiated (v) {
      Storage.set("initiated", v);
    },
  },

  methods: {
    getPayDay (num) {
      return this["payDay" + num].clone();
    },

    initiated () {
      this.isInitiated = true;
    },

    isValid () {
      return this.payDay1 != undefined &&
              this.payDay2 != undefined &&
                ![undefined, 0, null, "0.00", NaN]
                  .includes(this.income);
    },
  },
});




