import Vue from 'vue'
import VueRouter from 'vue-router';

import { routes } from './routes';

import App from './App.vue'

Vue.filter("to-currency", (value) => {
  let currency = "USD",
    lang = "en-US",
    style = "currency";

  let formattedValue = new Intl.NumberFormat(lang, {
    style: style,
    currency: currency,
  }).format(parseFloat(value).toFixed(2));

  return formattedValue;
});

Vue.use(VueRouter);

const router = new VueRouter({
  routes
});

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



